import Message from '../../message';
import MessageHandler from '../messageHandler';

export default class OpenSettingsHandler extends MessageHandler {
  getMessageType() {
    return Message.TYPE_SHOW_SETTINGS;
  }

  handle(messageData) {
    const handler = this;
    return handler.app.showSettings();
  }
}



