export default class Message {
	constructor(type, data = null) {
		this.type = type;
		this.data = data;
	}

	send() {
		const message = this;

		return new Promise(function(resolve, reject) {
			chrome.runtime.sendMessage({
				type: message.type,
				data: message.data
			}, function(reply) {
				resolve(reply);
			});
		});
	}
}

Message.TYPE_FETCH_ESPS = 'fetch_esps';
Message.TYPE_CHECK_VISIBILITY = 'check_visibility';
Message.TYPE_TIMEOUT = 'timeout';
Message.TYPE_TEST_LIFECYCLE = 'test_lifecycle';
Message.TYPE_RUN_TEST = 'run_test';
Message.TYPE_ACTIVATE_SIDEBAR = 'activate_sidebar';
Message.TYPE_SHOW_SETTINGS = 'show_settings';
Message.STORAGE_SET = 'storage_set';
Message.STORAGE_GET = 'storage_get';
Message.SET_ESP_ACCOUNT = 'set_esp_account';
Message.SESSION_CLEAR = 'clear_session';
Message.AUTORELOAD = 'autoreload';
Message.SEND_ANALYTICS = 'send_analytics';
Message.TYPE_LOCAL_IMAGE_FETCH = 'local_image_fetch';
Message.TYPE_HTML_FILE_CONTENT_FETCH = 'local_html_file_content_fetch';
Message.TYPE_BACKGROUND_JQUERY_AJAX_FETCH = 'background_jquery_ajax'

module.exports = Message;



