import Message from '../../message';
import MessageHandler from '../messageHandler';

export default class LocalHtmlFileContentFetchHandler extends MessageHandler {
  getMessageType() {
    return Message.TYPE_HTML_FILE_CONTENT_FETCH;
  }

  handle(htmlFileUrl) {
    return this._fetchFile(htmlFileUrl)
      .then((html) => {
        return html;
      })
      .catch(function(e) {
        console.log('something went wrong');
        console.log(e);
      });
  }

  _fetchFile(htmlFileUrl) {
    return new Promise(function (resolve, reject) {
      var request = new XMLHttpRequest();
      request.responseType = 'text';
      request.open('GET', htmlFileUrl, true);

      request.onload = function () {
        if (this.readyState === 4) {
          resolve(this.response);
        } else {
          reject({
            status: this.status,
            readyState: this.readyState,
            statusText: this.statusText
          });
        }
      };

      request.onerror = function () {
        reject({
          status: this.status,
          readyState: this.readyState,
          statusText: this.statusText
        });
      };

      request.send(null);
    });

  }
}



