import Message from '../../message';
import MessageHandler from '../messageHandler';

export default class ActivateSidebarHandler extends MessageHandler {
  getMessageType() {
    return Message.TYPE_ACTIVATE_SIDEBAR;
  }

  handle(messageData, sender) {
    const handler = this;
    const tab = sender.tab;

    return handler.app.handleClick(tab, 'beacon');
  }
}



