import CommonBrowserApi from "../commonBrowserApi"

export default class BackgroundBrowserApi extends CommonBrowserApi {
  getExtensionVersion() {
		return chrome.runtime.getManifest().version;
	}

  enableTab(tab) {
    chrome.browserAction.enable(tab.id);
  }

  setIcon(tab, activeState=false) {
    let small = activeState ? `images/icon19-active.png` : 'images/icon19.png';
    let large = activeState ? `images/icon38-active.png` : 'images/icon38.png';

    chrome.browserAction.setIcon({
      path: {
        '19': this.getURL(small),
        '38': this.getURL(large)
      },
      tabId: tab.id
    });
  }
}


