import Message from '../../message';
import MessageHandler from '../messageHandler';

export default class GetEspsHandler extends MessageHandler {
	getMessageType() {
		return Message.TYPE_FETCH_ESPS;
	}

	handle(messageData) {
		const handler = this;
		return handler.app.esps;
	}
}



