import Message from '../../message';
import MessageHandler from '../messageHandler';

export default class LocalImageFetchHandler extends MessageHandler {
  getMessageType() {
    return Message.TYPE_LOCAL_IMAGE_FETCH;
  }

  handle(imagePath) {
    var imageReq = this.fetchImage(imagePath);

    return this.fetchImage(imagePath)
      .then((response) => {
        return this.base64Image(response);
      })
      .catch(function(e) {
        console.log('something went wrong');
        console.log(e);
      });
  }

  fetchImage(imagePath) {
    return new Promise(function (resolve, reject) {
      var imageRequest = new XMLHttpRequest();
      imageRequest.responseType = 'blob';
      imageRequest.open('GET', imagePath, true);

      imageRequest.onload = function () {
        if (this.readyState === 4) {
          resolve(this.response);
        } else {
          reject({
            status: this.status,
            readyState: this.readyState,
            statusText: this.statusText
          });
        }
      };

      imageRequest.onerror = function () {
        reject({
          status: this.status,
          readyState: this.readyState,
          statusText: this.statusText
        });
      };

      imageRequest.send(null);
    });
  }

  base64Image(response) {
    return new Promise(function (resolve, reject) {
      var reader = new FileReader();

      reader.onloadend = (_dataUrl) => {
        resolve(reader.result);
      };

      reader.readAsDataURL(response);
    });
  }
}



