import Message from '../../message';
import MessageHandler from '../messageHandler';

export default class StorageGetHandler extends MessageHandler {
	getMessageType() {
		return Message.STORAGE_GET;
	}

	handle(messageData) {
		const handler = this;

    	const promise = new Promise(function(resolve, reject) {
    		chrome.storage.local.get(messageData, (data) => {
          if (chrome.runtime.lastError) {
            console.log('Storage GET error: ', chrome.runtime.lastError);
          }
        	resolve(data);
      	});
    	});

    	return promise;
	}
}



