import Message from '../../message';
import MessageHandler from '../messageHandler';
import caclulateIssuesCount from '../../issueCount';

export default class TestLifecycleHandler extends MessageHandler {

  constructor(app, backgroundBrowserApi) {
    super(app, backgroundBrowserApi);
    this.animationInterval = null;
  }

  getMessageType() {
    return Message.TYPE_TEST_LIFECYCLE;
  }

  clearIfAnitmationExists() {
    if (this.animationInterval) {
      clearInterval(this.animationInterval);
    }
  }

  setBadgeWithIssues(failedCount, tabId) {
    this.clearIfAnitmationExists();
    let color = failedCount > 0 ? '#FF0000' : '#2ECC71';
    chrome.browserAction.setBadgeText({text: failedCount.toString(), tabId: tabId});
    chrome.browserAction.setBadgeBackgroundColor({color: color, tabId: tabId});
  }

  setLoadingBadgeAnimation(tabId) {
    let count = 0;
    let animator = () => {
      switch (count) {
        case 0:
          chrome.browserAction.setBadgeText({text: '.  ', tabId: tabId});
          break;
        case 1:
          chrome.browserAction.setBadgeText({text: '.. ', tabId: tabId});
          break;
        case 2:
          chrome.browserAction.setBadgeText({text: '...', tabId: tabId});
          break;
      }
      count = (count + 1) % 3;
    };
    this.clearIfAnitmationExists();
    chrome.browserAction.setBadgeBackgroundColor({color: '#7E8C8D', tabId: tabId});

    this.animationInterval = setInterval(animator, 300);
  }

  setIcon(tab, active=false) {
    this.backgroundBrowserApi.setIcon(tab, active);
  }

  handle(messageData, sender) {
    const handler = this;
    const { hostname, pathname, protocol } = new URL(sender.tab.url);

    switch (messageData.status) {
      case 'running':
        this.setIcon(sender.tab);
        this.setLoadingBadgeAnimation(sender.tab.id);
        break;
      case 'completed':
        this.setIcon(sender.tab);

        let issuesCount = caclulateIssuesCount(messageData.result);
        let failedCount = issuesCount.total.failed;
        if (protocol === 'file:' || hostname === 'localhost' || hostname === '127.0.0.1' || messageData.noCounts) {
          this.clearIfAnitmationExists();
          chrome.browserAction.setBadgeText({text: '', tabId: sender.tab.id});
        } else {
          this.setBadgeWithIssues(failedCount, sender.tab.id);
        }
        break;
      case 'localActive':
        this.clearIfAnitmationExists();
        this.setIcon(sender.tab, true);
        chrome.browserAction.setBadgeText({text: '', tabId: sender.tab.id});
        break;
      case 'timeout':
        this.clearIfAnitmationExists();
        // Sets browser action icon and badge to normal state in case if they are already changed
        this.setIcon(sender.tab);
        chrome.browserAction.setBadgeText({text: '', tabId: sender.tab.id});
        break;
    }
  }
}



