import Message from "../../message";
import MessageHandler from "../messageHandler";
import Litmus from "../../../base/litmus";

let identity = require("../../../base/identity.js");
const Analytics = require("analytics-node");

export default class AnalyticsHandler extends MessageHandler {
  getMessageType() {
    return Message.SEND_ANALYTICS;
  }

  handle(options) {
    let analytics = this._segmentAnalytics()

    this._getInstallationId((installationId) => {
      let defaultProperties = {
        anonymousId: installationId,
        timestamp: new Date()
    };

      if(options.analyticsEventType === "identify") {
        let traits = options["traits"];
        traits["version"] = this.backgroundBrowserApi.getExtensionVersion();

        let params = { ...defaultProperties, ...{traits: traits} };
        analytics.identify(params);
      } else if(options.analyticsEventType === "track")  {
        analytics.track({ ...defaultProperties, ...options["eventData"] });
      }
    } );
  }

  _getInstallationId(callback) {
    identity.getInstallationId().then((installationId) => {
      callback(installationId);
    });
  }

  _segmentAnalytics() {
    if( this.analytics == null ) {
      this.analytics = new Analytics(this._segmentWriteKey(), { flushAt: 1 });
    }

    return this.analytics;
  }

  _segmentWriteKey() {
    return Litmus.ENV.segmentWriteKey();
  }
}



