import Message from '../../message';
import MessageHandler from '../messageHandler';

export default class StorageSetHandler extends MessageHandler {
	getMessageType() {
		return Message.STORAGE_SET;
	}

	handle(messageData) {
		const handler = this;
    // console.log('Storing...', messageData);
  	const promise = new Promise(function(resolve, reject) {
    		chrome.storage.local.set(messageData, (reply) => {
      		resolve(reply);
    		});
  	});

  	return promise;
	}
}



