import EventEmitter from '../../base/core/eventEmitter';
import Message from '../message';
import Litmus from "../../base/litmus"

import BackgroundBrowserApi from "./backgroundBrowserApi"

/** message handlers */
import GetEspsHandler from './handlers/getEsps';
import CheckVisibilityHandler from './handlers/checkVisibility';
import TestLifecycleHandler from './handlers/testLifecycle';
import StorageGetHandler from './handlers/storageGet';
import StorageSetHandler from './handlers/storageSet';
import SetESPAccountHandler from './handlers/set_esp_account';
import SessionHandler from './handlers/session';
import AutoReloadHandler from './handlers/autoReload';
import ActivateSidebarHandler from './handlers/activateSidebar';
import ShowSettingsHandler from './handlers/showSettings';
import AnalyticsHandler from './handlers/analytics';
import LocalImageFetchHandler from './handlers/localImageFetch';
import LocalHtmlFileContentFetchHandler from './handlers/localHtmlFileContentFetch';
import BackgroundJQueryAjaxHandler from './handlers/backgroundJQueryAjax';

/** ---------------- */

export default class App extends EventEmitter {
  static get BASE_URL() {
    return 'https://litmus.com';
  }

	constructor(config) {
		super();
		this.config = config;
		this.messageHandlers = {};
    this.autoReloadtimer = null;
    this.Litmus = Litmus;
    this.backgroundBrowserApi = new BackgroundBrowserApi();
	}

	start() {
		[
			GetEspsHandler,
			CheckVisibilityHandler,
			TestLifecycleHandler,
			StorageGetHandler,
			StorageSetHandler,
      SetESPAccountHandler,
      SessionHandler,
      AutoReloadHandler,
      ActivateSidebarHandler,
      ShowSettingsHandler,
      AnalyticsHandler,
      LocalImageFetchHandler,
      LocalHtmlFileContentFetchHandler,
      BackgroundJQueryAjaxHandler
		].forEach(HandlerClass => {
			const handler = new HandlerClass(this, this.backgroundBrowserApi);

			this.messageHandlers[handler.getMessageType()] = handler;
		});

		chrome.webRequest.onBeforeSendHeaders.addListener(this.setTypeKitReferrer, {
		    urls: ['*://use.typekit.net/*'],
		    types: ['stylesheet']
		}, ['requestHeaders','blocking']);

    // execute autoreload script on each local file tab
    this.startAutoReload();

		return this.fetchESPs()
			.then(esps => {
				this.esps = esps;
				this.setExtensionVisibilityForAllTabs();
			});
	}

  startAutoReload() {
    if (this.autoReloadtimer) {
      clearInterval(this.autoReloadtimer);
    }
    this.autoReloadtimer = setInterval(function(){
      chrome.tabs.getAllInWindow(null, function(tabs){
        for (var i = 0; i < tabs.length; i++) {
          if(tabs[i].url.includes("file://") || tabs[i].url.includes("localhost")) {
            chrome.tabs.executeScript(tabs[i].id, {
              file: "reloadMessages.js",
              allFrames: true
            });
          }
        }
      })
    }, 2000);
  }

  stopAutoReload() {
    clearInterval(this.autoReloadtimer);
  }

	setTypeKitReferrer(details) {
		var requestHeaders = details.requestHeaders;
			for (var i=0; i<requestHeaders.length; ++i) {
				if (requestHeaders[i].name.toLowerCase() === 'referer') {
					// The request was certainly not initiated by a Chrome extension...
					return;
				}
			}
			// Set Referer
			requestHeaders.push({
			name: 'referer',
			// Host must match the domain in your Typekit kit settings
      value: `https://${chrome.runtime.id}/`
		});

		return {
			requestHeaders: requestHeaders
		};
	}

	fetchESPs() {
		return new Promise(resolve => {
			const esps = require('../../base/providers');
			resolve(esps);
			return;
		});
	}

	setExtensionVisibilityForAllTabs() {
		// Update the extension visibility for all tabs after ESPs are fetched
		chrome.windows.getCurrent((currentWindow) => {
			chrome.tabs.query({ windowId: currentWindow.id }, (tabs) => {
				for (let tab of tabs) {
					this.setExtensionVisibility(tab);
				};
			});
    });
	}

	setExtensionVisibility(tab) {
    this.enableBrowserActionIcon(tab);
    this.isValidESP(tab, (yes) => {
      const { hostname, pathname, protocol } = new URL(tab.url);
      this.checkForWarnings(tab);
			// if (yes) {
			//   chrome.tabs.sendMessage(tab.id, {
   //          method: 'injectSidebar',
   //          url: tab.url
   //        }
   //      );
	  //   } else {
   //      //Dashboard here
   //      chrome.tabs.sendMessage(tab.id, {
   //          method: 'injectDashboardSidebar',
   //          url: tab.url
   //        }
   //      );
	  //   }
		});
	}

  checkForWarnings(tab) {
    if (localStorage['fileAccessOff'] && this.isLocal(tab)) {
      this.showIconWarning(tab.id);
    }
  }

  showIconWarning(tabId) {
    chrome.browserAction.setBadgeText({text: '!', tabId});
    chrome.browserAction.setBadgeBackgroundColor({color: '#FF0000', tabId});
  }

  enableBrowserActionIcon(tab) {
    this.backgroundBrowserApi.enableTab(tab);
    this.backgroundBrowserApi.setIcon(tab);
  }

  checkTabVisibility(tab, callback) {
    this.isValidESP(tab, (yes) => {
      callback(yes);
    });
  }

	isValidESP(tab, successCallback) {
		let { hostname, href, pathname, protocol } = new URL(tab.url);
		let match = false;

    // need to include all subdomains of exacttarget.com
    if (hostname.indexOf('exacttarget.com') > -1) {
      hostname = 'exacttarget.com';
    }

  	if (this.isLocal(tab)) {
	    // Enable the extension if it's a file: protocol
	    match = true;

	  } else {
	    // Enable the extension if we have an ESP for the domain
	    Object.keys(this.esps).forEach( domain => {
	    	const vals = this.esps[domain];

				// Continue unless hostname ends with this domain
	      if (hostname.indexOf(domain, hostname.length - domain.length) == -1) {
	      	return;
	      }

	      if (vals.active !== true) {
	      	return;
	      }

	      if (vals.path !== null) {
	        if (!pathname.match(vals.path) && (!href.match(vals.path))) {
	        	return;
	        }
	      }

	      match = true;
	    });
	  }

	  successCallback(match);
	}

  // removeESPAccountInfoForTab(tabId) {
  //   chrome.storage.local.get(['esp_accounts'],(items) => {
  //     console.log("Return from storage get", items);
  //     let list = items['esp_accounts'] || [];
  //     const newList = list.filter( item => { return item.tabId != tabId });
  //     console.log("setting esp_accounts", newList);
  //     chrome.storage.local.set({
  //         esp_accounts: newList
  //       }, (reply) => {
  //         console.log(`esp_accounts entry removed for tab ${tabId} !!!`);
  //     });
  //   });
  // }

	listen() {
		chrome.browserAction.onClicked
			.addListener(this.handleClick.bind(this));

		if (chrome.runtime.onInstalled) {
			chrome.runtime.onInstalled
				.addListener(this.handleInstall.bind(this));
		}

		chrome.tabs.onUpdated
			.addListener(this.handleUpdate.bind(this));

		chrome.runtime.onMessage
			.addListener(this.handleMessage.bind(this));

    this._setupUninstallPage();

    chrome.storage.onChanged
          .addListener(this.handleStorageChange.bind(this));

    this.checkForFileAccessOption();
	}

  checkForFileAccessOption() {
    let _self = this;
    chrome.extension.isAllowedFileSchemeAccess(function(answer){
      if(answer) {
        if(localStorage['fileAccessOff']) {
          localStorage.removeItem('fileAccessOff');
          _self.showStartPage(2);
          _self.refreshAllLocal();
        }
      } else {
        localStorage['fileAccessOff'] = true;
      }
    });
  }

  // refresh all windows with local files open
  refreshAllLocal() {
    chrome.tabs.query({}, function(tabs)
    {
      for(var i = 0; i < tabs.length; i++)
      {
        if(tabs[i].url.includes("file://") || tabs[i].url.includes("localhost")) {
          chrome.tabs.update(tabs[i].id, {url: tabs[i].url});
        }
      }
    });
  }

  handleStorageChange(changes, areaName) {
    if (areaName == "local") {
      // console.log("Storage got changed", changes);
      if (changes.hasOwnProperty('esp_accounts')) {
        chrome.tabs.query({}, (tabs) => {
          tabs.map( (tab ) => {
            chrome.tabs.sendMessage(tab.id, {
              method: 'espAccountUpdated',
              esp_accounts: changes['esp_accounts'].newValue
            }, (reply) => {
            });
          })
        });
      }

      if (changes.hasOwnProperty('current_account')) {
        chrome.tabs.query({}, (tabs) => {
          tabs.map( (tab ) => {
            chrome.tabs.sendMessage(tab.id, {
              method: 'checklist-loginStatus'
            }, (reply) => {
            });
          })
        });
      }
    }
  }

	// Show sidebar when extension icon is clicked
	handleClick(tab, via = 'icon') {
    if (localStorage['fileAccessOff'] && this.isLocal(tab)) {
      this.showStartPage();
      return;
    }

    let newInstalled = chrome.storage.sync.get('newInstalled', (data) => {
      if(data.newInstalled) {
        chrome.storage.sync.set({'newInstalled': false });
      }
    });
	  chrome.tabs.sendMessage(tab.id, {
		  	method: 'sidebarInjected?',
		  	url: tab.url
		  },
		  (yes) => {
		  	if(yes){
		  		chrome.tabs.sendMessage(tab.id, {
		  		  	method: 'toggleSidebar',
		  		  	url: tab.url
		  		  }, () => {
              chrome.tabs.sendMessage(tab.id, {
                  method: 'initAutoloader',
                  url: tab.url
                }
              );
            }
		  		);
		  	}else{
          this.isValidESP(tab, (valid) => {
            let method = valid ? 'injectSidebar' : 'injectDashboardSidebar';
            // log which sidebar we're rendering
            chrome.tabs.sendMessage(tab.id, {
              method: 'recordPageAction',
              action: 'extension_sidebar_rendered',
              attributes: {
                sidebar_type: (valid ? 'valid_esp' : 'dashboard'),
                via: via
              }
            });

            chrome.tabs.sendMessage(tab.id, {
                method: method,
                url: tab.url,
                fileAccess: !localStorage['fileAccessOff']
              },
              () => {
                // A hack for waiting React to load so that there is no white flash in-between
                window.setTimeout(() => {
                  chrome.tabs.sendMessage(tab.id, {
                      method: 'toggleSidebar',
                      url: tab.url
                    }
                  );

                  chrome.tabs.sendMessage(tab.id, {
                      method: 'initAutoloader',
                      url: tab.url
                    }
                  );
                }, 500);
              }
            );
          });
		  	}
		  }
	  );
	}

  showStartPage(step=0) {
    if( !this.Litmus.ENV.isProduction() ) {
      return;
    }

    let path = 'startpage/index.html';
    if (step > 0) {
      path += `#step-${step}`;
    }
    chrome.tabs.create({url: this.backgroundBrowserApi.getURL(path)});
  }

  showSettings() {
    chrome.tabs.create({ url: `chrome://extensions/?id=${chrome.runtime.id}`, active: true });
  }

	// Displays the welcome screen on install
	handleInstall(object) {
	  if (object.reason == 'install') {
      this.showStartPage();
      chrome.storage.sync.set({'newInstalled': true });

      let messageType = Message.SEND_ANALYTICS
      this.messageHandlers[messageType].handle({analyticsEventType: "track", eventData: {event: "installed"}})
	  }
	}

	// Update extension visibility when a tab is updated
	handleUpdate(tabId, changeInfo, tab) {
		function getParameterByName(name, url) {
	    if (!url) url = window.location.href;
	    name = name.replace(/[\[\]]/g, "\\$&");
	    var regex = new RegExp("[?&]" + name + "(=([^&#]*)|&|#|$)"),
	        results = regex.exec(url);
	    if (!results) return null;
	    if (!results[2]) return '';
	    return decodeURIComponent(results[2].replace(/\+/g, " "));
		}

		if ( getParameterByName("display", tab.url) == "true") {
			this.handleClick(tab);
		}

	  if (changeInfo.status == 'complete') {

	    this.setExtensionVisibility(tab);

      let { hostname, href, pathname, protocol } = new URL(tab.url);
      if(this.isLocal(tab) && !localStorage['fileAccessOff'])  {
        chrome.tabs.sendMessage(tab.id, {
                    method: "injectBeacon",
                    url: tab.url,
                    fileAccessOff: localStorage['fileAccessOff']
                  },
                  () => {});
      }
	  }
	}

	handleMessage(message, sender, sendResponse) {
		const msg = new Message(message.type, message.data);

		let isAsync = false;
		let isReplySent = false;
		let handler;

		function reply(data) {
			isReplySent = true;
			sendResponse(data);
		}

		if (undefined === this.messageHandlers[msg.type]) {
			// console.error('Unknown message type:', msg.type);

		} else {
			let handlerReply;

			try {
				handlerReply = this.messageHandlers[msg.type].handle(msg.data, sender);

				if (handlerReply instanceof Promise) {
					isAsync = true;

					handlerReply
						.then(function(response) {
							reply(response);
						})
						.catch(function(error) {
							console.error('Handler error:', error);
							// @todo some error handling?
							reply();
						});
				} else {
					reply(handlerReply);
				}
			} catch (e) {
				// @todo some error handling?
				reply();
			}
		}

		if (!isAsync) {
			reply();
		}

		return isAsync;
	}

  isLocal(tab) {
    let { hostname, href, pathname, protocol } = new URL(tab.url);
    return (protocol == 'file:' || hostname == 'localhost' || hostname == '127.0.0.1') && /^html{0,1}$/.test(pathname.split('.').pop())
  }

  _setupUninstallPage() {
    if(this.Litmus.ENV.isProduction()) {
      if (chrome.runtime.setUninstallURL) {
        chrome.runtime.setUninstallURL('http://litmus.com/extension/uninstall');
      }
    }
  }
}



