  export default function calculateIssuesCount(testResult) {
    const issueCounts = {};

    issueCounts['firstImpressions'] = {passed: 0, failed: 0};
    if (testResult.subject_line.result.result_type === 'Success') {
      issueCounts['firstImpressions']['passed'] += 1;
    } else {
      issueCounts['firstImpressions']['failed'] += testResult.subject_line.result.issues.length;
    }

    if (testResult.from.result.result_type === 'Success') {
      issueCounts['firstImpressions']['passed'] += 1;
    } else {
      issueCounts['firstImpressions']['failed'] += testResult.from.result.issues.length;
    }

    if (testResult.reply_to.result.result_type === 'Success') {
      issueCounts['firstImpressions']['passed'] += 1;
    } else {
      issueCounts['firstImpressions']['failed'] += testResult.reply_to.result.issues.length;
    }

    if (testResult.preview_text.result.result_type === 'Success') {
      issueCounts['firstImpressions']['passed'] += 1;
    } else {
      issueCounts['firstImpressions']['failed'] += testResult.preview_text.result.issues.length;
    }

    // Skip issue counting for content session all together by setting 0s
    issueCounts['content'] = {
      passed: 0,
      failed: 0
    };

    issueCounts['links'] = {passed: 0, failed: 0};
    testResult.links.link_checks.forEach((link) => {
      if (link.valid) {
        issueCounts['links']['passed'] += 1;
      } else {
        issueCounts['links']['failed'] += 1;
      }
    });

    issueCounts['tracking'] = {
      passed: testResult.tracking.result.error_checks_passed,
      failed: (
        testResult.tracking.result.error_checks_ran -
        testResult.tracking.result.error_checks_passed
      )
    };

    issueCounts['images'] = {passed: 0, failed: 0};
    testResult.images.image_checks.forEach((image) => {
      if (image.errored) {
        issueCounts['images']['failed'] += 1;
      } else {
        issueCounts['images']['passed'] += 1;
      }
    });


    let totalPassed = 0, totalFailed = 0;
    for (var sectionName in issueCounts) {
      if (!issueCounts.hasOwnProperty(sectionName)) {
        continue;
      }

      totalPassed += issueCounts[sectionName]['passed'];
      totalFailed += issueCounts[sectionName]['failed'];
    }

    issueCounts['total'] = {
      passed: totalPassed,
      failed: totalFailed,
      total: totalPassed + totalFailed
    };

    return issueCounts;
  }



