import Message from '../../message';
import MessageHandler from '../messageHandler';

export default class CheckVisibilityHandler extends MessageHandler {
  getMessageType() {
    return Message.TYPE_CHECK_VISIBILITY;
  }

  handle(messageData, tab) {
    const handler = this;

    const promise = new Promise(function(resolve, reject) {
      handler.app.isValidESP(tab, (yes) => {
        resolve(yes);
      });
    });

    return promise;
  }
}



