import Message from '../../message';
import MessageHandler from '../messageHandler';

export default class SessionHandler extends MessageHandler {
  getMessageType() {
    return Message.SESSION_CLEAR;
  }

  handle(messageData) {
    const handler = this;

    const promise = new Promise(function(resolve, reject) {
      chrome.cookies.remove({ url: messageData.url, name: '_litmus_secure_session_v3' },
        function (cookie) {
          if (!cookie) {
            console.log('Can\'t get cookie! Check the name!');
          }
        });
      });

    return promise;
  }
}



