import Message from '../../message';
import MessageHandler from '../messageHandler';

export default class AutoReloadHandler extends MessageHandler {
  getMessageType() {
    return Message.AUTORELOAD;
  }

  handle(messageData) {
    const handler = this;
    if (messageData.action === 'start') {
      return handler.app.startAutoReload();
    } else if (messageData.action === 'stop') {
      return handler.app.stopAutoReload();
    }
  }
}



