import Message from '../../message';
import MessageHandler from '../messageHandler';

export default class SetESPAccountHandler extends MessageHandler {
  getMessageType() {
    return Message.SET_ESP_ACCOUNT;
  }

  handle(messageData, sender) {
    const handler = this;
    // console.log('Storing...', messageData, sender.tab.id);
    const promise = new Promise(function(resolve, reject) {
      chrome.storage.local.get(['esp_accounts'],(items) => {
        console.log('Return from storage get', items);
        let uniqueId = messageData['name'] + messageData['email'];
        let list = items['esp_accounts'] || [];
        if ( list.filter( esp => {
 return esp.id === uniqueId; 
}).length > 0 ) {
          return;
        }
        let data = {...messageData, tabId: sender.tab.id, id: uniqueId};
        const newList = [...list, data];
        // console.log('setting esp_accounts', newList);
        chrome.storage.local.set({
            esp_accounts: newList
          }, (reply) => {
            // console.log('esp_accounts set!!!');
            resolve(reply);
        });
      });
    });

    return promise;
  }
}



